	module.exports = {"data":{"site":{"siteMetadata":{"title":"Shang-Yu Su","subtitle":"PhD student at National Taiwan University, research interests cover Deep Learning, Natural Language Processing, and dialogue systems.","copyright":"© All rights reserved.","author":{"name":"Shang-Yu Su","twitter":"#"},"disqusShortname":"","url":"https://www.shangyusu.com"}},"markdownRemark":{"id":"/Users/ShangYu/Desktop/github/shangyusu.github.io/src/pages/articles/AAAI2019-DSTC7-track1-2/index.md absPath of file >>> MarkdownRemark","html":"<p><b>This paper is published in the 7th Dialog System Technology Challenge (DSTC7) in the Proceedings of Thirty-Third AAAI Conference on Artificial Intelligence (AAAI 2019 - DSTC7).</b></p>\n<p>Full paper:\n<a href=\"/AAAI_2019___DSTC_Track_1_2-1c0c2c056933540e847486427a9bfc64.pdf\" target=\"_blank\">Here</a></p>\n<p>With the increasing research interest in dialogue response generation, there is an emerging branch formulating this task as selecting next sentences, where given the partial dialogue contexts, the goal is to determine the most probable next sentence.\nFollowing the recent success of the Transformer model, this paper proposes (1) a new variant of attention mechanism based on multi-head attention, called highway attention, and (2) a recurrent model based on transformer and the proposed highway attention, so-called Highway Recurrent Transformer.\nExperiments on the response selection task in the seventh Dialog System Technology Challenge (DSTC7) show the capability of the proposed model of modeling both utterance-level and dialogue-level information; the effectiveness of each module is further analyzed as well.</p>","fields":{"tagSlugs":["/tags/dialogue/","/tags/response-selection/"]},"frontmatter":{"title":"Learning Multi-Level Information for Dialogue Response Selection by Highway Recurrent Transformer","tags":["Dialogue","Response Selection"],"date":"2018-12-5T22:40:32.169Z","description":"Ting-Rui Chiang, Chao-Wei Huang, Shang-Yu Su, and Yun-Nung Chen","conference":"AAAI 2019 - DSTC7"}}},"pathContext":{"slug":"/posts/aaai2019-dstc7-hrt/"}}

